import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { SET_DATA } from 'store/actions';

// material-ui
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { LoadingButton } from '@material-ui/lab';
import { Grid, Table, TableContainer, TableBody, TableHead, TableRow, TableCell, Typography, Slider, TextField, Chip} from '@material-ui/core';
import StockChartCard from 'views/dashboard/Default/StockChartCard';
import MainCard from 'ui-component/cards/MainCard';
import { useHistory } from 'react-router-dom';
import SaveIcon from '@material-ui/icons/Save';

// project imports
import { gridSpacing } from 'store/constant';

import StockCard from 'views/dashboard/Default/StockCard';
import SubCard from 'ui-component/cards/SubCard'
import configData from 'config';
import { makeStyles, useTheme } from '@material-ui/styles';
import ChartData from 'views/dashboard/Default/chart-data/radarchart';
import Chart from 'react-apexcharts';
import axios from 'axios';
//==============================|| TYPOGRAPHY ||==============================//

const AccountPage = () => {
    const [isLoading, setLoading] = useState(false);
    const [dataset, setDataset] = useState({});
    const [positions, setPositions] = useState(false);

    const account = useSelector((state) => state.account);
    let { id } = useParams();
    const theme = useTheme();
    let history = useHistory();

    const [value, setValue]  = useState({});
    const [tempValue, setTempValue]  = useState({});

    useEffect(() => {
        const url = configData.API_SERVER+"users/accounts/"+id;
        axios.get(url, { headers: { Authorization: `${account.token}` } })
            .then((response) => setDataset(response.data))
    }, []);

    useEffect(() => {
        const url = configData.API_SERVER+"users/accounts/"+id+"/positions";
        axios.get(url, { headers: { Authorization: `${account.token}` } })
            .then((response) => setPositions(response.data))
    }, []);

    // useEffect(() => {
    //     if (!dataset.subscriptions){
    //         return false;
    //     }
    //     let m = 0;
    //     for (let i = 0; i < dataset.subscriptions.length; i++) {
    //         if (dataset.subscriptions[i].id > m){
    //             m = dataset.subscriptions[i].id;
    //         }
    //     }
    //     console.log(m);
    //     setValue(Array(m).fill(0));
    //     setTempValue(Array(m).fill(0))
    // }, [dataset]);

    useEffect(() => {
        if (!dataset.subscriptions){
            return false;
        }
        let mapper = {};
        for (let i = 0; i < dataset.subscriptions.length; i++) {
            mapper[dataset.subscriptions[i].strategy.id] = dataset.allocation[dataset.subscriptions[i].strategy.id] || 0;
        }
        setValue(mapper);
        setTempValue(mapper);
        // console.log(dataset);
        // console.log(mapper);
    }, [dataset]);


    const committed = () => {
        setValue(tempValue);
    }

    const saveAllocation = () => {
        setLoading(true)
        const url = configData.API_SERVER+"users/accounts/"+id;
        axios.post(url, value, { headers: { Authorization: `${account.token}` } })
            .then(() => setLoading(false))

        // console.log(value);
    }

    // const handleChange = (t, index) => {
    //     let temp = [...tempValue];
    //     temp[index] = t;

    //     const leftover = 100 - t;
    //     const denom = temp.reduce((partialSum, a) => partialSum + a, 0) - t;
    //     let scale = 0;
    //     if (denom !== 0){
    //         scale = leftover/denom;
    //     }
        
    //     for (let i = 0; i<value.length; i++){
    //         if (i!==index){
    //             temp[i] = scale*temp[i];
    //         }
    //     }
    //     setTempValue(temp);
    // }

    const handleChange = (t, index) => {
        // console.log(t);
        // console.log(index);
        let temp = {...tempValue};
        temp[index] = t;
        console.log(temp);

        const leftover = 100 - t;
        let denom = 0;
        for (const [key, value] of Object.entries(temp)) {
            denom = denom + value;
        }
        denom = denom - t;
        
        let scale = 0;
        if (denom !== 0){
            scale = leftover/denom;
        }
        console.log(scale);
        
        for (let i = 0; i < Object.keys(value).length; i++){
            let j = Object.keys(value)[i];
            if (String(j) !== String(index)){
                temp[j] = scale*temp[j];
            }
        }
        console.log(temp);
        setTempValue(temp);
    }



    return (
            <Grid container>
                { dataset.public_key ?
                <Grid container item xs={12} spacing={gridSpacing}>
                    <Grid item md={6} xs={12} spacing={gridSpacing}>
                        <Grid item xs={12} sx={{mb: 4}}>
                            <SubCard 
                                title={
                                    <Grid container>
                                        <Grid item xs={6}>
                                            <Typography variant="h2">
                                                {dataset.name} 
                                            </Typography>
                                            <Typography variant="h4" >
                                                {dataset.public_key}
                                            </Typography>
                                        </Grid>
                                        <Grid container item xs={6} justifyContent="right">
                                            <img height={50} width={50} src={dataset.icon}></img>
                                        </Grid>
                                    </Grid>
                                }>
                                <Grid container>
                                    <Grid container item xs={6}>
                                        <Typography variant="h1" >
                                            {dataset.info.equity} 
                                        </Typography>
                                    </Grid>
                                    <Grid container item xs={6} justifyContent="right">
                                        {dataset.info.side ? <Chip label={dataset.info.change} color="success"/>
                                        : (dataset.info.side === false) ? <Chip label={dataset.info.change} color="error"/>
                                        : <></>
                                        }
                                    </Grid>
                                </Grid>
                            </SubCard>
                        </Grid>
                        {positions ? 
                        <Grid item xs={12}>
                            <SubCard title={
                                <Typography variant="h2" >
                                    Positions
                                </Typography>}>
                            
                                <TableContainer>
                                    <Table>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell align="left">Asset</TableCell>
                                                <TableCell align="left">Quantity</TableCell>
                                                <TableCell align="left">Unrealized P/L</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {positions.map((row) => (
                                                <TableRow
                                                key={row.symbol}
                                                >
                                                    <TableCell align="left" component="th"><Typography variant="h4">{row.symbol}</Typography></TableCell>
                                                    <TableCell align="left"><Typography variant="h5">{row.quantity}</Typography></TableCell>
                                                    <TableCell align="left"><Typography variant="h5">{row.pl}</Typography></TableCell>
                                                </TableRow>
                                            ))}
                                        </TableBody>
                                    </Table>
                                </TableContainer>

                                {/* <Chart {...ChartData()} /> */}
                            </SubCard>
                        </Grid>
                        : <></>}
                    </Grid>
                    <Grid item md={6} xs={12} >
                        <SubCard
                        title={
                            <div>
                                <Typography variant="h1" display="inline" >
                                    Linked Strategies
                                </Typography>
                                <LoadingButton sx={{float: "right"}} loading={isLoading} loadingPosition="start" startIcon={<SaveIcon/>} variant="contained" component="label" onClick={() => saveAllocation()}>
                                    Save
                                </LoadingButton>
                            </div>
                            }>
                            <Grid container item xs={12} spacing={gridSpacing}>
                                <Grid item xs={12}>
                                    {dataset.subscriptions.map(item => (
                                        <SubCard 
                                            title={
                                                <div>
                                                    <Typography variant="h2" display="inline">
                                                        {item.strategy.name} 
                                                    </Typography>
                                                    <Chip sx={{float: "right", p: 0}} icon={<ArrowBackIcon />} label="Learn more" variant="outlined" 
                                                        onClick={() => history.push("/utils/strategies/"+item.strategy.id)}
                                                    />
                                                </div>
                                            }
                                            sx={{my: 2}}>
                                            {Object.keys(value).length>0 && <Slider
                                                // getAriaLabel={() => console.log(item.strategy.id)}
                                                value={tempValue[item.strategy.id]}
                                                onChange={(t) => handleChange(t.target.value, item.strategy.id)}
                                                onChangeCommitted={committed}
                                                valueLabelDisplay="auto"
                                                disableSwap
                                                />}
                                            
                                        </SubCard>
                                    ))}
                                </Grid>
                            </Grid>
                        </SubCard>
                    </Grid>
                </Grid>: <></>
                }
            </Grid>
    );
};

export default AccountPage;
