//-----------------------|| DASHBOARD - BAJAJ AREA CHART ||-----------------------//

const ChartData = ({extra = [], extra2 = []}) => {
    let height = 200;
    if (extra2 && extra2.length != 0){
        height = 400;
    }
    // let extra3 = [];
    // for (let i = 0; i<extra2.length; i++){
    //     const arr = [extra2[i][1]-100, extra2[i][1]+100];
    //     extra3.push({
    //         x: extra2[i][0], 
    //         y: arr
    //     });
    // }
    
    return {
        type: 'area',
        height: height,
        options: {
            colors: ['#1e88e5', '#808080'],
            chart: {
                // id: 'support-chart',
                sparkline: {
                    enabled: true
                },
                animations: {
                    enabled: false
                },

            },
            // dataLabels: {
            //     enabled: false
            // },
            stroke: {
                curve: 'smooth',
                width: 3
            },
            xaxis: {
                type: 'datetime',
                labels: {show: true},
                // axisTickers: {show: true}
            },    
            tooltip: {
                // theme: 'dark',
                shared: true,
                fixed: {
                    enabled: true,
                    position: 'topLeft'
                },
                x: {
                    show: true,
                    format: 'MMM dd yyyy' // 'MMM dd yyyy, HH:mm:ss'
                },
                y: {
                    title: {
                        formatter: (seriesName) => seriesName+' Value'
                    }
                },
                // marker: {
                //     show: false
                // }
            },
            // tooltip: {
            //     fixed: {
            //         enabled: false
            //     },
            //     x: {
            //         show: true,
            //     },
            //     y: {
            //         title: {
            //             formatter: (seriesName) => 'Account Value'
            //         }
            //     },
            //     // marker: {
            //     //     show: false
            //     // }
            // },
            // xaxis:{
            //     labels: {
            //         show: true,
            //     }                
            // },
        },
        series: [
                {
                    name: "Strategy",
                    data: extra
                },
                {
                    name: "Benchmark",
                    data: extra2
                },
                // {
                //     name: "Standard Deviation",
                //     type: 'rangeArea',
                //     data: extra3
                // }
            ],
        }
    };

export default ChartData;
