import React from 'react';

// material-ui
import { makeStyles, useTheme } from '@material-ui/styles';
import { Card, CardContent, Grid, Typography } from '@material-ui/core';

// third-party
import Chart from 'react-apexcharts';

// project imports
import ChartData from 'views/dashboard/Default/chart-data/stock-chart';

// style constant
const useStyles = makeStyles((theme) => ({
    card: {
        backgroundColor: theme.palette.secondary.light
    },
    content: {
        padding: '0px !important'
    },
    contentContainer: {
        padding: '16px',
        paddingBottom: 0,
        color: '#fff'
    },
    fontStyle: {
        fontWeight: 400
    }
}));


const StockChartCard = ({ annual_ret, dataset, white, benchmark, title }) => {
    const classes = useStyles();
    const theme = useTheme();

    const orangeDark = theme.palette.secondary[800];

    // console.log(dataset);
    return (
        <Card className={white ? "": classes.card}>
            <CardContent className={classes.content}>
                <Grid container className={classes.contentContainer}>
                    <Grid item xs={12}>
                        <Grid container alignItems="center" justifyContent="space-between">
                            {white ? 
                            <Typography variant="subtitle1" sx={{ color: theme.palette.secondary.dark }}>
                                { title ? title : '' }
                            </Typography>
                            :<Grid item>
                                <Typography variant="subtitle1" sx={{ color: theme.palette.secondary.dark }}>
                                    Historical Performance
                                </Typography>
                            </Grid>}
                            <Grid item>
                                {annual_ret && annual_ret < 0 ?
                                <Typography variant="h4" sx={{ color: theme.palette.error.main }}>
                                    {Math.round(annual_ret*100)/100}%
                                </Typography> : annual_ret ?
                                <Typography variant="h4" sx={{ color: theme.palette.success.main }}>
                                    {Math.round(annual_ret*100)/100}%
                                </Typography>
                                : <></>}
                                {annual_ret ?
                                <Typography alignItems="right" variant="subtitle2" sx={{ color: theme.palette.grey[800] }}>annually</Typography>
                                : <></>}
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <Chart {...ChartData({extra: dataset, extra2: benchmark})} />
            </CardContent>
        </Card>
    );
};

export default StockChartCard;
