import PropTypes from 'prop-types';
import React from 'react';

// material-ui
import { makeStyles } from '@material-ui/styles';
import { CardContent, Grid, Typography, Chip } from '@material-ui/core';

// project imports
import StockChartCard from 'views/dashboard/Default/StockChartCard';
import MainCard from 'ui-component/cards/MainCard';
import SkeletonPopularCard from 'ui-component/cards/Skeleton/PopularCard';
import { gridSpacing } from 'store/constant';

// style constant
const useStyles = makeStyles((theme) => ({
    cardAction: {
        padding: '10px',
        paddingTop: 0,
        justifyContent: 'center'
    },
    primaryLight: {
        color: theme.palette.primary[200],
        cursor: 'pointer'
    },
    divider: {
        marginTop: '12px',
        marginBottom: '12px'
    },
    avatarSuccess: {
        width: '16px',
        height: '16px',
        borderRadius: '5px',
        backgroundColor: theme.palette.success.light,
        color: theme.palette.success.dark,
        marginLeft: '15px'
    },
    successDark: {
        color: theme.palette.success.dark
    },
    avatarError: {
        width: '16px',
        height: '16px',
        borderRadius: '5px',
        backgroundColor: theme.palette.orange.light,
        color: theme.palette.orange.dark,
        marginLeft: '15px'
    },
    errorDark: {
        color: theme.palette.orange.dark
    }
}));

//-----------------------|| DASHBOARD DEFAULT - POPULAR CARD ||-----------------------//

const StockCard = ({ isLoading, data, backtest, subscribed}) => {
    const classes = useStyles();

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    // var mergedTimes = [];
    // for (var i = 0; i<data["graphs"]["Equity Curve"].length; i++){
    //     mergedTimes.push(data["graphs"]["Equity Curve"][i]);
    // }
    return (
        <React.Fragment>
            {isLoading ? (
                <SkeletonPopularCard backtest={backtest} />
            ) : (
                <MainCard content={false} sx={{cursor: "pointer"}}>
                    <CardContent>
                        <Grid container spacing={gridSpacing}>
                            <Grid item xs={12}>
                                <Grid container alignContent="center" justifyContent="space-between">
                                    <Grid item xs={12} height={"32px"}>
                                        <Typography display="inline" variant="h4">{data.name}</Typography>
                                        {subscribed && <Chip label="Subscribed" color="primary" sx={{float: "right"}}/>}                                        
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12} sx={{ pt: '16px !important', textAlign:"center", flexDirection: "column" }}>
                                {(!data["results"]["graphs"]["chart"].length) ?                             
                                <Typography variant="h4">ðŸ“ˆ</Typography>
                                : <StockChartCard annual_ret ={data["results"]["stats"] ? data["results"]["stats"]["annualized_return"]: 0} dataset={data["results"]["graphs"]["chart"]} />
                                }
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="column">
                                    {
                                    Object.entries(data["results"]["stats"])
                                    .map(([key, value]) =>
                                    <Grid item key={key}>
                                        <Grid container alignItems="center" justifyContent="space-between">
                                            <Grid item>
                                                <Typography variant="subtitle1" color="inherit">
                                                    {key}
                                                </Typography>
                                            </Grid>
                                            <Grid item>
                                                <Grid container alignItems="center" justifyContent="space-between">
                                                    <Grid item>
                                                        <Typography variant="subtitle1" color="inherit">
                                                            {Math.round(value * 100) / 100}
                                                        </Typography>
                                                    </Grid>
                                                    {/* <Grid item>
                                                        <Avatar variant="rounded" className={classes.avatarSuccess}>
                                                            <KeyboardArrowUpOutlinedIcon fontSize="small" color="inherit" />
                                                        </Avatar>
                                                    </Grid> */}
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                

                                    )}

                                    
                                </Grid>
                            </Grid>
                        </Grid>
                    </CardContent>
                    {/* <CardActions className={classes.cardAction}>
                        <Button size="small" disableElevation>
                            View All
                            <ChevronRightOutlinedIcon />
                        </Button>
                    </CardActions> */}
                </MainCard>
            )}
        </React.Fragment>
    );
};

StockCard.propTypes = {
    isLoading: PropTypes.bool
};

export default StockCard;
